@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* No CSS *//*# sourceMappingURL=App.css.map */
* {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  overflow-x: hidden;
}

.nav {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  background-color: #fff;
}

.nav li {
  text-decoration: none;
  list-style: none;
  margin: 10px;
  padding: 10px;
  font-weight: 500;
  cursor: pointer;
}

.nav li:hover {
  color: #3b3054;
}

.nav .left {
  display: flex;
  justify-content: flex-start;
  flex: 1 1;
}

.nav .right {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 20px;
}

.nav .right .signup {
  background-color: #2acfcf;
  color: #fff;
  border-radius: 55px;
}

.nav .right .signup:hover {
  opacity: 0.5;
}

.mobileNav {
  display: none;
}

.navItems {
  width: 300px;
  height: 250px;
  border-radius: 10px;
  background-color: #3b3054;
  color: #fff;
  margin: 0 auto;
  display: none;
}

.navItems li {
  padding: 10px;
  text-decoration: none;
  list-style: none;
  text-align: center;
  cursor: pointer;
}

.navItems li:hover {
  color: #bfbfbf;
}

.navItems .line {
  width: 90%;
  border-bottom: 2px solid #bfbfbf;
  margin: 0 auto;
  opacity: 0.5;
}

.navItems .signup {
  background-color: #2acfcf;
  color: #fff;
  border-radius: 55px;
  width: 50%;
  margin: 0 auto;
}

@media screen and (max-width: 780px) {
  nav {
    display: none;
  }
  .mobileNav {
    display: block;
  }
}
/*# sourceMappingURL=Nav.css.map */
.header {
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
  margin: 5% 15%;
}

.header p {
  color: #bfbfbf;
}

.header h1 {
  color: #3b3054;
}

.header .header-left {
  width: 40%;
}

.header .header-left button {
  margin: 10px 0;
  color: #fff;
  background-color: #2acfcf;
  border-radius: 50px;
  border: 0;
  padding: 10px 20px;
  cursor: pointer;
}

.header .header-left button:hover {
  opacity: 0.5;
}

.header .header-right {
  width: 60%;
}

.header .header-right img {
  width: 350px;
  height: 320px;
}

@media screen and (max-width: 780px) {
  .header {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
    margin: 10%;
  }
  .header-left {
    margin-right: 5%;
  }
}
/*# sourceMappingURL=Header.css.map */
::-webkit-input-placeholder {
  color: #35323e;
  padding: 0 10px;
}

:-ms-input-placeholder {
  color: #35323e;
  padding: 0 10px;
}

::-ms-input-placeholder {
  color: #35323e;
  padding: 0 10px;
}

::placeholder {
  color: #35323e;
  padding: 0 10px;
}

.detailPart {
  width: 100%;
  min-height: 600px;
  max-height: -webkit-max-content;
  max-height: -moz-max-content;
  max-height: max-content;
  background-color: #f0f0f0;
  overflow: hidden;
}

.shorten-form {
  background-color: #3b3054;
  min-width: 70%;
  max-width: -webkit-max-content;
  max-width: -moz-max-content;
  max-width: max-content;
  min-height: 100px;
  max-height: -webkit-max-content;
  max-height: -moz-max-content;
  max-height: max-content;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
}

.shorten-form input {
  border-radius: 5px;
  border: 0;
  width: 70%;
  height: 40px;
}

.shorten-form button {
  background-color: #2acfcf;
  color: #fff;
  border: 0;
  border-radius: 50px;
  padding: 10px;
  margin: 10px;
  cursor: pointer;
}

.shorten-form button:hover {
  opacity: 0.5;
}

.detail {
  width: 40%;
  margin: 10% auto;
}

.detail h1 {
  color: #3b3054;
}

.detail p {
  color: #35323e;
}

.statinfo {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin: 10% 0;
}

.statinfo .statone,
.statinfo .stattwo,
.statinfo .statthree {
  color: #3b3054;
  margin: 10px;
  width: 300px;
  min-height: 200px;
  max-height: -webkit-max-content;
  max-height: -moz-max-content;
  max-height: max-content;
  background-color: #fff;
  border-radius: 5px;
  overflow: hidden;
  z-index: 2;
}

.statinfo .statone h4,
.statinfo .statone p,
.statinfo .stattwo h4,
.statinfo .stattwo p,
.statinfo .statthree h4,
.statinfo .statthree p {
  margin: 12% 10%;
}

.statinfo .statone p,
.statinfo .stattwo p,
.statinfo .statthree p {
  font-size: 12px;
  color: #35323e;
}

.statinfo .statone img,
.statinfo .stattwo img,
.statinfo .statthree img {
  display: block;
  margin: 0 auto;
  background-color: #3b3054;
  padding: 10px;
  border-radius: 50%;
}

.statinfo .line {
  width: 50%;
  border: 4px solid #2acfcf;
  position: absolute;
  margin: 100px auto;
  z-index: 1;
}

.getstarted {
  width: 100%;
  min-height: 100px;
  max-height: -webkit-max-content;
  max-height: -moz-max-content;
  max-height: max-content;
  background: url(/shortly-react/static/media/boost-desktop.dc8b886b.svg);
  background-size: 1900px;
  background-color: #3b3054;
  text-align: center;
  padding: 10% 0;
  color: #fff;
}

.getstarted button {
  background-color: #2acfcf;
  border-radius: 50px;
  border: 0;
  padding: 10px;
  cursor: pointer;
  color: #fff;
}

.getstarted button:hover {
  opacity: 0.5;
}

@media screen and (max-width: 960px) {
  .statinfo {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
  }
  .statinfo .statone,
  .statinfo .stattwo,
  .statinfo .statthree {
    color: #3b3054;
    margin: 10px auto;
    width: 300px;
    min-height: 200px;
    max-height: -webkit-max-content;
    max-height: -moz-max-content;
    max-height: max-content;
    background-color: #fff;
    border-radius: 5px;
    overflow: hidden;
    z-index: 2;
  }
  .linetwo {
    border-left: 6px solid #2acfcf;
    height: 500px;
    position: absolute;
    margin: 0 50%;
    z-index: 1;
  }
  .line {
    display: none;
  }
}

@media screen and (max-width: 550px) {
  .shorten-form button {
    padding: 6px;
  }
}
/*# sourceMappingURL=Detail.css.map */
